.about-me {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: $light-color;

  p {
    margin: 0;
    margin-bottom: 20px;
    font-style: italic;
    font-weight: 400;
    // font-size: 16px;
    // line-height: 180%;
    font-size: 20px;
    line-height: 163.9%;
    letter-spacing: 0.02em;
    color: $dark-color;
  }

  &__image {
    display: inline-block;

    &--float-left {
      margin-right: 30px;
      margin-bottom: 30px;
      float: left;
    }
    &--float-right {
      margin-left: 30px;
      margin-bottom: 30px;
      float: right;
    }

    img {
      max-width: 100%;
      object-fit: cover;
    }
  }
}
.about-me-images {
  gap: 22px;

  &__item {
    &:first-child {
      grid-column: 4 span;
    }
    &:nth-child(2) {
      grid-column: 3 span;
    }
    &:nth-child(3) {
      grid-column: 5 span;
    }
    &:nth-child(n + 4) {
      grid-column: 6 span;
    }
  }
  &__foto {
    display: block;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
