.workshops {
  padding: $section-offset 0;

  &__title {
    margin: 0;
    margin-bottom: $gutter;
    font-style: italic;
    font-weight: 600;
    font-size: 34px;
    line-height: 159%;
    text-transform: uppercase;
    color: $light-color;
  }

  &__descr {
    margin: 0;
    margin-bottom: $section-offset;
    font-weight: 400;
    font-size: 24px;
    line-height: 142%;
    letter-spacing: 0.01em;
    color: $light-color;
  }

  &__item {
    grid-column: 3 span;
  }

  &__item .workshops-card {
    height: 100%;
  }
}

.workshops-card {
  display: flex;
  flex-direction: column;

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    padding: 42px 10px;
    max-height: 274px;
    height: 100%;
    border-radius: 20px;
    font-size: 0;
    background-color: $bg-gray-color;
  }

  &__image {
    display: block;
    font-size: 0;
    height: 100%;
    text-align: center;
    overflow: clip ;

    img {
      height: 100%;
      max-width: 100%;
      object-fit: scale-down;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: $light-color;
  }

  &__descr {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: $light-color;
  }

  &__price {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 800;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0.02em;
    color: $light-color;
  }

  &__btns {
    @include flex-all-sb;
  }

  &__btn {
    padding: 16px 16px;
    // border-radius: 10px;
    width: 100%;
    font-size: 16px;
    text-align: center;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}
