.link {
  position: relative;
  z-index: 5;
  font-weight: 500;
  font-size: $main-size;
  line-height: 145%;
  color: inherit;
  cursor: pointer;
  transition: color .3s ease-out;

  svg {
    fill: currentColor;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    color: $accent-color;
  }

  &:active {
    color: $active-color;
  }
}
.link.focus-visible  {
  color: $accent-color;
}
