.breadcrumbs {
  @include flex-v-center;
  flex-wrap: wrap;
  margin-bottom: 50px;


  &__item {
    color: $light-color;
    &:not(:first-child) {
      &::before{
        content: '>';
        margin: 0 6px;
        color: currentColor;
      }
    }
    &:last-child {
      color: rgba($light-color, 0.5);
    }
  }

  &__link {
    position: relative;
    z-index: 2;
    font-weight: 500;
    font-size: 14px;
    color: inherit;

    &:not([href]) {
      color: inherit;
      cursor: default;
    }

    &[href]::before {
      content: '';
      position: absolute;
      top: -4px;
      left: -7px;
      z-index: -1;
      width: calc(100% + 14px);
      height: calc(100% + 8px);
      background-color: rgba($dark-color, 0.8);
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out, background-color .3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      &[href] {
        color: $accent-color;
      }
    }

    &:active {
      &[href] {
        color: $active-color;
      }
    }
  }
}
.breadcrumbs__link.focus-visible {
  &[href] {
    color: $accent-color;
  }
}

