.prodact-card {
  padding-bottom: 100px;
  background-color: $bg-dark-color;

  &__title {
    margin-bottom: 40px;
  }
}
.prodact-card-images {
  grid-column: 5 span;
  overflow: hidden;
}
.prodact-card-images-slider {
  min-height: 465px;
}
.card-img {
  // padding: 20px;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  background-color: rgb(42, 43, 47, 0.5);
  cursor: pointer;
  outline: 2px solid red;

  &__pic {
    display: block;
    padding: 20px;
    text-align: center;

    img {
      object-fit: cover;
    }
  }
}

.prodact-card-images-nav {
  overflow: hidden;
  max-height: 96px;

  .swiper-slide {
    padding: 20px;
    border-radius: 10px;
    opacity: 0.6;
    background-color: rgb(42, 43, 47, 0.5);
    transition: opacity 0.3s ease-in-out;
    cursor: pointer;
    outline: 2px solid red;

    .swiper-slide-thumb-active {
      opacity: 1;
    }

    &__pic {
      display: block;
      width: 100%;
      height: 100%;
      text-align: center;

      img {
        object-fit: scale-down;
      }
    }
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }
}

