.about-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;

  &__img {
    margin-bottom: 18px;
  }
  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 20px;
    line-height: 34px;
    color: $light-color;
  }
  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: $main-size;
    line-height: 28px;
    text-align: center;
    color: $light-color;
  }
}

