.dev {
  height: calc(100vh - 200px);
  max-height: 900px;
  color: $light-color;
  background-color: $bg-dark-color;

  overflow: hidden;

  &__container {
    padding-top: 100px;
    // @include flex-v-center;

    // flex-direction: column;
    width: 100%;
    height: 100%;
  }
  &__img {
    grid-column: 4 span;
  }
  &__text-content {
    @include flex-v-center;
    flex-direction: column;
    grid-column: 8 span;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    width: 100%;
    font-weight: 400;
    font-size: 60px;
    line-height: 145%;
    text-align: center;
    color: inherit;
  }

  &__btn {
    padding: 20px 30px;
    font-size: 20px;
  }
}
