.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 20px;
  max-height: 500px;

  &::before {
    content: '';
    @include absolute-full;
    z-index: 3;
    background-color: rgba($dark-color, 0.6);
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &--played {
    &::before {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10;
    border-radius: 100%;
    width: 80px;
    height: 80px;
    color: $light-color;
    background-color: rgba($dark-color, 0.6);
    transform: translate(-50%, -50%);

    &:hover {
      background-color: $accent-color;
    }

    &:active {
      background-color: rgba($active-color, 0.6);
    }

    svg {
      position: absolute;
      top: 50%;
      left: calc(50% + 3px);
      width: 26px;
      height: 26px;
      fill: currentColor;
      transform: translate(-50%, -50%);
    }


    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }
  // &__mited {
  //   position: absolute;
  //   left: 32px;
  //   bottom: 32px;
  //   z-index: 10;
  //   border-radius: 100%;
  //   width: 40px;
