.promo {
  padding: 100px 0 0;
  color: $light-color;
  background-color: $bg-dark-color;
  background-image: url(../img/promo/promo-bg.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top -24px right;
  z-index: -1;
  max-height: 800px;
  overflow: hidden;


  &__container {
    align-items: center;
  }

  &__content {
    position: relative;
    margin-right: 40px;
    max-width: 52%;
    // max-width: 65%;

    &::before {
      // content: url(../img/svg/promo-bg1.svg);
      content: url(../img/promo/promo-bg2.svg);
      position: absolute;
      top: -38px;
      left: 65px;
      transform: rotate(2deg);
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 18px;
    font-style: italic;
    font-weight: 600;
    font-size: 90px;
    line-height: 104%;
    color: $light-color;
    text-transform: uppercase;

    &--ofset{
      padding-left: $section-offset;
      font-style: normal;
      font-weight: 800;
      font-size: 90px;
      line-height: 104%;
      text-transform: none;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 56px;
    max-width: 616px;
    font-weight: 500;
    font-size: 18px;
    line-height: 160%;
    color: $light-color;
  }

  &__backlinks {
    align-items: center;
  }

  &__btn {
    // margin-right: 80px;
    padding: 26px 32px;
    font-size: 20px;
  }

  &__list {
    align-items: center;
    gap: 30px;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-weight: 500;
    font-size: $main-size;
    line-height: 145%;
    color: $light-color;
    transform: scale(1);
    transition: color .3s ease-in-out, transform .3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: $active-color;
    }
  }

  &__icon {
    fill: currentColor;
  }

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
  }
  &__img {
    object-fit: cover;
    width: 100%;
    transform: scale(1.1) translateY(-1%);
  }
}
.promo__link.focus-visible  {
  transform: scale(1.15);
  color: $accent-color;
}

.promo-altai {
  position: relative;
  // padding: 100px 0 0;
  // color: $light-color;
  background-color: $bg-dark-color;
  height: 100vh;
  max-height: 900px;
  overflow: hidden;

  &--bg {
    background-image: url(../img/altai/bg-altai.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
  }
  &__container {
    @include flex-all-center;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  &__title {
    margin: 0;
    padding-top: 40px;
    padding-bottom: 25px;
    width: 100%;
    font-style: italic;
    text-transform: capitalize;
    color: $light-color;

    span {
      display: block;
      font-weight: 400;
      font-size: 96px;
      line-height: 145%;
      text-align: right;

      &:first-child {
        font-weight: 700;
        font-size: 128px;
        text-align: left;
      }
    }
  }
}
