.btn {
  border-radius: 40px;
  border: 2px solid transparent;
  font-weight: 500;
  font-size: 14px;
  line-height: 143%;
  letter-spacing: 0.02em;
  color: $light-color;
  text-decoration: none;
  transition: background-color .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;

  &--stroke {
    font-weight: 500;
    border-color: $accent-color;
    color: $light-color;
    background-color: transparent;
    transition: background-color .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;

    @include hover {
      border-color: transparent;
      color: $dark-color;
      background-color: $active-color;
    }

    &:active {
      border-color: $active-color;
      color: $light-color;
      background-color: transparent;
    }
  }
  &--fill {
    font-weight: 800;
    // color: $dark-color;
    color: $light-color;
    background-color: $accent-color;
    transition: background-color .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;

    @include hover {
      color: $dark-color;
      background-color: $active-color;
    }

    &:active {
      border-color: $active-color;
      color: $light-color;
      background-color: transparent;
    }
  }
  &:focus {
    outline: none;
  }
}

.btn.focus-visible  {
  color: $dark-color;
  background-color: $active-color;
}
