.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  padding: $section-offset 0;
  width: 100%;
  color: $light-color;
  // background-color: $bg-dark-color;

  .site-container--page-product & {
    position: relative;
    background-color: $bg-dark-color;
  }
  .site-container--page-altai & {
    // position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    color: $dark-color;
    background-color: rgba(159, 171, 186, 0.3);
  }

  &__container {
    align-items: center;
  }
}

.logo {
  display: inline-block;
  margin-right: auto;
  font-size: 0;
  color: inherit;


  &:focus {
    outline: none;
  }

  &__img {
    fill: currentColor;
    stroke: currentColor;
  }
}

