.lang {
  @include flex-all-center;

  &__link {
    display: block;
    padding: 4px 10px;
    padding-top: 6px;
    border-radius: 40px;
    border: 2px solid transparent;
    font-weight: 500;
    font-size: 14px;
    line-height: 143%;
    letter-spacing: 0.02em;
    color: inherit;
    opacity: 0.3;
    background-color: transparent;

    &--current {
      // font-weight: 800;
      border-color: inherit;
      color: inherit;
      background-color: transparent;
      opacity: 1;
      transition: background-color .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;

      // @include hover {
      //   border-color: $accent-color;
      //   background-color: $accent-color;
      // }

      // &:active {
      //   border-color: $active-color;
      //   color: $light-color;
      //   background-color: transparent;
      // }
    }

    @include hover {
      opacity: 1;
      border-color: $accent-color;
      background-color: $accent-color;
    }

    &:active {
      opacity: 1;
      border-color: inherit;
      color: inherit;
      background-color: transparent;
    }
  }
  span {
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;
    opacity: 0.3;
  }
}
